export default {
	components: {
	},
	props: {
		value: [Number, String, Object],
		list: {
			type: Array,
			default: ()=> {
				return []
			}
		},
		title: [Number, String],
		realState: Boolean,
		realNum:Number,
		bFlag:Number
	},
	data() {
		return {
			showList: "",
			showClass: '',
			selectItem: {},
			contentTop: 0,
			ownNum:'',
			changeNum:''
		}
	},
	computed:{
		getCommonFlag(){
			return this.$store.state.commonFlag
		}
	},
	watch: {
		realState(){
			this.ownNum = this.realNum;
			if(this.ownNum == 1){
				this.close()
			}else{
				return
			}
		},
		sortFlag(){
			
		},
		getCommonFlag(){
			this.close();
		}
	},
	mounted() {
		this.showList = this.active;
		this.selectItem = this.list[this.value];
		this.changeNum = this.bFlag;
	},
	methods: {
		choose(item) {
			this.selectItem = item
			this.$emit('input', item.value)
			this.closePopup()
		},
		changePopup() {			
			this.ownNum = 2
			this.changeNum = 2;
			var moveFlag;
			this.$emit('state',this.changeNum)
			if(this.showList) {	
				moveFlag = false;
				this.$emit('moveFlag',moveFlag)
				this.closePopup()					
			} else {
				moveFlag = true;
				this.$emit('moveFlag',moveFlag)
				this.openPopup()
			}
			setTimeout(() => {
				this.$emit('flag',[this.showList,this.ownNum])
			}, 300)
		},
		openPopup() {				
			this.$parent.$emit('close')
			this.showList = true
			this.$nextTick(() => {
				this.getElementData('.dropdown-item__selected', (data)=>{
					this.contentTop = data[0].bottom
					this.showClass = 'show'
				})
			})
		},
		closePopup() {
			this.showClass = ''
			setTimeout(() => {
				this.showList = false
			}, 300)
		},
		close() {
			this.showClass = ''
			this.showList = false
		},
		getElementData(el, callback){
			uni.createSelectorQuery().in(this).selectAll(el).boundingClientRect().exec((data) => {
				callback(data[0]);
			});
		}
	}
}
